import dnIcon from '../dn-icon/dn-icon.vue';
import uniicon from '../../components/uni-icons/uni-icons.vue'
import item from './item.vue';
import refresh from '../refresh/refresh.vue';
import loadmore from '../uni-load-more/uni-load-more.vue';
export default {
	name: 'review',
	props: {
		reviewMsg: [Array],
		childData: [Array],
		clickComment: Object
	},
	components: {
		uniicon,
		dnIcon,
		item,
		refresh,
		loadmore
	},
	data() {
		return {
			// 弹出列表详情动画
			animationData: '',
			// 弹出列表详情开关
			crControl: false,
			// 获取节点高度
			setHeight: '',
			// 上拉加载
			more: 'more',
		}
	},
	onLoad() {

	},
	mounted() {
		this.animation = uni.createAnimation();
		const query = uni.createSelectorQuery().in(this);
		query.select('#id').boundingClientRect(data => {
			console.log("得到布局位置信息" + JSON.stringify(data));
			this.setHeight = data.height;
		}).exec();
	},
	methods: {
		commentMe(obj){
			this.$emit('comment',obj)

		},
		childReview(item, key) {
			this.crControl = true;
			this.animation.translateY(this.setHeight).step();
			this.animationData = this.animation.export();
			this.$nextTick(function(){

				this.animation.translateY(0).step({ duration: 150 });
				this.animationData = this.animation.export();
			})
			this.$emit('childReview', item);
		},
		closeCr() {
			this.crControl = false;
		},
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e);
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
		isRefresh() {
			let that = this;
			setTimeout(() => {
				uni.showToast({
					icon: 'none',
					title: '刷新成功'
				})
				this.$refs.refresh.endAfter() //刷新结束
			}, 1000)
		},
		loadMore() {
			this.more = "loading";
			setTimeout(() => {
				this.more = "noMore";
			}, 1000);
		}
	}
}
